<template>
  <div class="bodyContent">
    <!-- S1: Ads Detailed -->
    <v-row>
      <v-col cols="12" align="start">
        <p class="header_form">รายละเอียดการโฆษณาและรูปภาพ</p>
        <DisplayImages :imagesArray="s1_ads_photoShow" />
        <v-textarea
          class="mt-4 px-4 rounded-custom"
          label="ข้อความที่สงสัยว่าโฆษณาเกินจริง"
          v-model="s1_ads_detail"
          filled
          background-color="grey lighten-4"
          readonly
          rounded
          outlined
          no-resize
          rows="3"
        ></v-textarea>
      </v-col>
    </v-row>

    <v-divider></v-divider>
    <!-- S2: Product Detailed -->
    <v-row>
      <v-col cols="12" align="start">
        <p class="header_form">รายละเอียดผลิตภัณฑ์ที่โฆษณา</p>
        <p class="sub-header_form">ประเภทผลิตภัณฑ์</p>
        <v-row class="mx-4">
          <v-col cols="12" md="6" class="py-0">
            <v-text-field
              label="ประเภทของผลิตภัณฑ์"
              v-model="s2_pd_detail.pd_pdTypeName"
              background-color="grey lighten-4"
              readonly
              outlined
              rounded
            ></v-text-field>
          </v-col>
        </v-row>
        <p class="sub-header_form">ชื่อผลิตภัณฑ์</p>
        <v-row class="mx-4">
          <v-col cols="12" md="6" class="py-0">
            <v-text-field
              label="ชื่อผลิตภัณฑ์ (ทั่วไป)"
              v-model="s2_pd_detail.pd_nameTH"
              background-color="grey lighten-4"
              readonly
              outlined
              rounded
            ></v-text-field>
          </v-col>
          <v-col cols="12" md="6" class="py-0">
            <v-text-field
              label="ชื่อสามัญ (ภาษาไทย)"
              v-model="s2_pd_detail.pd_cNameTH"
              background-color="grey lighten-4"
              readonly
              outlined
              rounded
            ></v-text-field>
          </v-col>
          <v-col cols="12" md="6" class="py-0">
            <v-text-field
              label="ชื่อสามัญ (ภาษาอังกฤษ)"
              v-model="s2_pd_detail.pd_cNameEN"
              background-color="grey lighten-4"
              readonly
              outlined
              rounded
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row class="mx-4">
          <v-col cols="12" md="6" class="py-0">
            <v-text-field
              label="ชื่อทางการค้า (ภาษาไทย)"
              v-model="s2_pd_detail.pd_fNameTH"
              background-color="grey lighten-4"
              readonly
              outlined
              rounded
            ></v-text-field>
          </v-col>
          <v-col cols="12" md="6" class="py-0">
            <v-text-field
              label="ชื่อทางการค้า (ภาษาอังกฤษ)"
              v-model="s2_pd_detail.pd_fNameEN"
              background-color="grey lighten-4"
              readonly
              outlined
              rounded
            ></v-text-field>
          </v-col>
        </v-row>
      </v-col>
    </v-row>

    <v-divider></v-divider>
    <!-- S3: Source of Product Detailed -->
    <v-row>
      <v-col cols="12" align="start">
        <p class="header_form">รายละเอียดแหล่งที่มา/จุดเผยแพร่โฆษณา</p>
        <DisplayImages :imagesArray="s3_src_photoShow" />
        <p class="sub-header_form">ประเภทแหล่งที่มา/จุดเผยแพร่โฆษณา</p>
        <v-row class="mx-4">
          <v-col cols="12" md="6" class="py-0">
            <v-text-field
              label="ประเภทของแหล่งที่มา"
              v-model="s3_src_detail.src_srcTypeName"
              background-color="grey lighten-4"
              readonly
              outlined
              rounded
            ></v-text-field>
          </v-col>
        </v-row>

        <!-- S3.1: About Source -->
        <p class="sub-header_form">เกี่ยวกับแหล่งที่มา/จุดเผยแพร่</p>
        <v-textarea
          class="mt-4 px-8 rounded-custom"
          label="เกี่ยวกับแหล่งที่มา/จุดที่เผยแพร่"
          v-model="getAboutSource"
          filled
          background-color="grey lighten-4"
          readonly
          rounded
          outlined
          no-resize
          rows="3"
        >
        </v-textarea>
        <p class="sub-header_form">ตำแหน่ง/สถานที่พบจุดเผยแพร่</p>

        <!-- S3.2: Location Info -->
        <v-row class="mx-4">
          <v-col cols="12" class="py-0">
            <v-text-field
              label="ที่อยู่"
              v-model="s3_src_detail.src_adrs"
              background-color="grey lighten-4"
              readonly
              outlined
              rounded
            ></v-text-field>
          </v-col>
        </v-row>
        <v-row class="mx-4">
          <v-col cols="6" md="6" class="py-0">
            <v-text-field
              label="จังหวัด"
              v-model="s3_src_detail.src_prov"
              background-color="grey lighten-4"
              readonly
              outlined
              rounded
            ></v-text-field>
          </v-col>
          <v-col cols="6" md="6" class="py-0">
            <v-text-field
              label="อำเภอ/เขต"
              v-model="s3_src_detail.src_dist"
              background-color="grey lighten-4"
              readonly
              outlined
              rounded
            ></v-text-field>
          </v-col>
          <v-col cols="6" md="6" class="py-0">
            <v-text-field
              label="ตำบล/แขวง"
              v-model="s3_src_detail.src_subDist"
              background-color="grey lighten-4"
              readonly
              outlined
              rounded
            ></v-text-field>
          </v-col>
        </v-row>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import DisplayImages from "./DisplayImages";
import moment from "moment";
export default {
  components: {
    DisplayImages
  },
  props: [
    "s1_ads_detail",
    "s2_pd_detail",
    "s3_src_detail",
    "s1_ads_photoShow",
    "s3_src_photoShow"
  ],
  computed: {
    getAboutSource() {
      return (
        this.s3_src_detail.src_subSrcTypeName +
        " " +
        this.s3_src_detail.src_subInfoName +
        " " +
        this.computedDateFormattedMomentjs() +
        " " +
        this.s3_src_detail.src_timeOnAir +
        " " +
        this.s3_src_detail.src_pName +
        " " +
        this.s3_src_detail.src_pTel +
        " " +
        this.s3_src_detail.src_pContact +
        " " +
        this.s3_src_detail.src_pLicense +
        " " +
        this.s3_src_detail.src_pCar +
        " " +
        this.s3_src_detail.src_pCarColor
      );
    }
  },
  methods: {
    computedDateFormattedMomentjs() {
      let originYear = this.s3_src_detail.src_dateOnAir.substr(0, 4);
      let cvYearInt = parseInt(originYear);
      if (cvYearInt < 2000) {
        return "";
      }
      let cvYearBE = (cvYearInt += 543);
      let displayDate = this.s3_src_detail.src_dateOnAir.replace(
        originYear,
        cvYearBE
      );
      return this.s3_src_detail.src_dateOnAir
        ? moment(displayDate)
            .locale("th")
            .format("LL")
        : "";
    }
  }
};
</script>

<style scoped>
.bodyContent {
  width: 100%;
  max-width: 1264px;
  margin: auto;
}
.header_form {
  font-size: 24px;
  font-weight: 500;
  color: #00b5f8;
}
.sub-header_form {
  font-size: 18px;
  font-weight: 600;
  color: #424242;
}
.rounded-custom {
  border-radius: 12px;
}
</style>
