<template>
  <v-container class="fill-height fill-width">
    <v-row class="text-left" align="center" justify="center">
      <v-col class="ma-0 pa-0 pl-6">
        <div
          v-if="
            this.userAuth.userID.length > 0 &&
              this.userAuth.userToken.length > 0
          "
        >
          <div class="bodyContent">
            <!-- Show dialog when click create new report -->
            <v-dialog v-model="dialog_createCase" max-width="400">
              <v-card flat align="center" class="rounded-custom">
                <v-container class="pa-6">
                  <v-icon
                    class="mt-12 hidden-sm-and-down"
                    align="center"
                    color="light-blue lighten-2"
                    size="60"
                  >
                    fa-file
                  </v-icon>
                  <p class="text_headerDialog pt-6">สร้างรายงาน</p>
                  <span class="text_content mx-6 hidden-sm-and-down"
                    >กรุณาเลือกประเภทของรายงาน</span
                  ><br />
                  <span class="text_content mx-6 hidden-sm-and-down"
                    >โดยรายงานจะถูกทำการบันทึกเป็นฉบับร่าง</span
                  >
                  <div v-for="(choice, index) in choice_caseType" :key="index">
                    <v-row align="center" justify="center" class="my-3">
                      <v-btn
                        depressed
                        block
                        rounded
                        large
                        class="my-3"
                        color="primary"
                        @click="requestDraftCase(choice.caseTypeID)"
                      >
                        <v-row align="center" justify="center">
                          <v-icon
                            left
                            small
                            class="mx-3"
                            size="24"
                            color="white"
                            >{{ choice.icon }}</v-icon
                          >

                          {{ choice.title }}
                        </v-row>
                      </v-btn>
                      <span class="mx-2 text_subChoice">{{ choice.info }}</span>
                    </v-row>
                    <!-- <v-divider class="my-2"></!-->
                  </div>

                  <v-card-actions>
                    <v-btn
                      color="#D9534F"
                      class="mb-1"
                      text
                      large
                      block
                      rounded
                      @click="dialog_createCase = false"
                    >
                      ยกเลิก
                    </v-btn>
                  </v-card-actions>
                </v-container>
              </v-card>
            </v-dialog>

            <!-- Show report info. dialog fullscreen  -->
            <v-dialog
              v-model="displayCase"
              fullscreen
              hide-overlay
              transition="dialog-bottom-transition"
            >
              <v-card>
                <v-toolbar flat dark color="primary">
                  <v-btn icon dark @click="displayCase = false">
                    <v-icon>fa-times</v-icon>
                  </v-btn>
                  <v-toolbar-title>{{ dp_title }}</v-toolbar-title>
                </v-toolbar>
                <v-container>
                  <div v-if="dp_caseTypeID == 1">
                    <v-row justify="center" class="pa-4">
                      <ReportSymptom
                        :s1_pt_info="s1_pt_info"
                        :s1_pt_address="s1_pt_address"
                        :s1_his_arr_disease="s1_his_arr_disease"
                        :s1_his_arr_drugAllegy="s1_his_arr_drugAllegy"
                        :s1_pt_history="s1_pt_history"
                        :s1_photos_show="s1_photos_show"
                        :s1_st_info="s1_st_info"
                        :s1_st_discovery="s1_st_discovery"
                        :s2_3_information="s2_3_information"
                      />
                    </v-row>
                  </div>

                  <div v-else-if="dp_caseTypeID == 2">
                    <v-row justify="center" class="pa-4">
                      <ReportProduct
                        :s1_user_info="s1_user_info"
                        :s1_pd_discover="s1_pd_discover"
                        :s2_pd_detail="s2_pd_detail_full"
                        :s2_pd_label="s2_pd_label"
                        :s2_pd_licenseInfo="s2_pd_licenseInfo"
                        :s2_pd_manufacturer="s2_pd_manufacturer"
                        :s2_pd_usage="s2_pd_usage"
                        :s2_pd_check="s2_pd_check"
                        :s2_pd_checkInfo="s2_pd_checkInfo"
                        :s2_drugSet="s2_drugSet"
                        :s2_testkit="s2_testkit"
                        :s3_src_detail="s3_src_detail"
                        :s2_pd_photoShow="s2_photos_show"
                        :s3_src_photoShow="s3_photos_show"
                      />
                    </v-row>
                  </div>

                  <div v-else-if="dp_caseTypeID == 3">
                    <v-row justify="center" class="pa-4">
                      <ReportFakeAds
                        :s1_ads_detail="s1_ads_detail"
                        :s2_pd_detail="s2_pd_detail"
                        :s3_src_detail="s3_src_detail"
                        :s1_ads_photoShow="s1_photos_show"
                        :s3_src_photoShow="s3_photos_show"
                      />
                    </v-row>
                  </div>
                </v-container>
              </v-card>
            </v-dialog>

            <!-- USERPROFILE, DASHBOARD -->
            <v-row justify="center" align="center">
              <!-- USERPROFILE -->
              <v-col cols="12" md="3">
                <v-row>
                  <v-col align="center" md="12">
                    <v-card flat class="card_section1" max-height="500">
                      <div align="center">
                        <v-avatar size="150">
                          <v-img :src="this.userInfo.FbAvater" contain></v-img>
                        </v-avatar>
                      </div>
                      <div align="center" class="ma-4">
                        <span class="text_title"
                          >{{ this.userInfo.FName }}
                          {{ this.userInfo.LName }}</span
                        >
                        <br />
                        <span class="text_subTitle">{{
                          this.userInfo.RoleName
                        }}</span>
                      </div>
                      <div align="left" class="pt-2">
                        <span class="text_attribute">อีเมล </span>
                        <span class="text_subTitle">{{
                          this.userInfo.Email
                        }}</span>
                        <br />
                        <span class="text_attribute">หน่วยงาน </span>
                        <span class="text_subTitle">{{
                          this.userInfo.OrganizationName
                        }}</span>
                        <br />
                        <span class="text_attribute">พื้นที่เขต </span>
                        <span class="text_subTitle">{{
                          this.userInfo.AreaName
                        }}</span>
                      </div>
                      <v-btn
                        class="mt-6"
                        depressed
                        block
                        large
                        rounded
                        color="primary"
                        @click="dialog_createCase = true"
                      >
                        <v-icon left class="mr-3" color="#FFFFFF"
                          >fa-plus</v-icon
                        >
                        สร้างรายงาน
                      </v-btn>
                    </v-card>
                  </v-col>
                </v-row>
              </v-col>
              <!-- DASHBOARD BOX -->
              <v-col cols="12" md="9">
                <v-row>
                  <v-col align="center" md="4">
                    <v-card
                      outlined
                      class="card_section1"
                      min-width="200"
                      max-width="250"
                      max-height="250"
                    >
                      <p class="text_subQuestion mb-0">รายงานทั้งหมด</p>
                      <p
                        class="text_question mb-0"
                        :style="{ color: '#00B5F8' }"
                      >
                        {{ this.caseOverall.CASE_ALL }}
                      </p>

                      <v-row align="center" justify="center">
                        <span class="text_attribute mr-3">UN: </span>
                        <span class="text_subQuestion mr-4">
                          {{ this.caseOverall.ALL_UN }}</span
                        >
                        <span class="text_attribute mr-3">PD: </span>
                        <span class="text_subQuestion mr-4">{{
                          this.caseOverall.ALL_PD
                        }}</span>
                        <span class="text_attribute mr-3">AD: </span>
                        <span class="text_subQuestion">{{
                          this.caseOverall.ALL_AD
                        }}</span>
                      </v-row>
                    </v-card>
                  </v-col>
                  <v-col align="center" md="4">
                    <v-card
                      outlined
                      class="card_section1"
                      min-width="200"
                      max-width="250"
                      max-height="250"
                    >
                      <p class="text_subQuestion mb-0">ฉบับร่าง</p>
                      <p
                        class="text_question mb-0"
                        :style="{ color: '#BDBDBD' }"
                      >
                        {{ this.caseOverall.ALL_DRAFT }}
                      </p>

                      <v-row align="center" justify="center">
                        <span class="text_attribute mr-3">UN: </span>
                        <span class="text_subQuestion mr-4">
                          {{ this.caseOverall.UN_DRAFT }}</span
                        >
                        <span class="text_attribute mr-3">PD: </span>
                        <span class="text_subQuestion mr-4">{{
                          this.caseOverall.PD_DRAFT
                        }}</span>
                        <span class="text_attribute mr-3">AD: </span>
                        <span class="text_subQuestion">{{
                          this.caseOverall.AD_DRAFT
                        }}</span>
                      </v-row>
                    </v-card>
                  </v-col>
                  <v-col align="center" md="4">
                    <v-card
                      outlined
                      class="card_section1"
                      min-width="200"
                      max-width="250"
                      max-height="250"
                    >
                      <p class="text_subQuestion mb-0">รอการตอบรับ</p>
                      <p
                        class="text_question mb-0"
                        :style="{ color: '#FBC02D' }"
                      >
                        {{ this.caseOverall.ALL_WAITING }}
                      </p>

                      <v-row align="center" justify="center">
                        <span class="text_attribute mr-3">UN: </span>
                        <span class="text_subQuestion mr-4">
                          {{ this.caseOverall.UN_WAITING }}</span
                        >
                        <span class="text_attribute mr-3">PD: </span>
                        <span class="text_subQuestion mr-4">{{
                          this.caseOverall.PD_WAITING
                        }}</span>
                        <span class="text_attribute mr-3">AD: </span>
                        <span class="text_subQuestion">{{
                          this.caseOverall.AD_WAITING
                        }}</span>
                      </v-row>
                    </v-card>
                  </v-col>
                </v-row>
                <v-row>
                  <v-col align="center" md="4">
                    <v-card
                      outlined
                      class="card_section1"
                      min-width="200"
                      max-width="250"
                      max-height="250"
                    >
                      <p class="text_subQuestion mb-0">กำลังดำเนินการ</p>
                      <p
                        class="text_question mb-0"
                        :style="{ color: '#FFB74D' }"
                      >
                        {{ this.caseOverall.ALL_PROGRESS }}
                      </p>

                      <v-row align="center" justify="center">
                        <span class="text_attribute mr-3">UN: </span>
                        <span class="text_subQuestion mr-4">
                          {{ this.caseOverall.UN_PROGRESS }}</span
                        >
                        <span class="text_attribute mr-3">PD: </span>
                        <span class="text_subQuestion mr-4">{{
                          this.caseOverall.PD_PROGRESS
                        }}</span>
                        <span class="text_attribute mr-3">AD: </span>
                        <span class="text_subQuestion">{{
                          this.caseOverall.AD_PROGRESS
                        }}</span>
                      </v-row>
                    </v-card>
                  </v-col>
                  <v-col align="center" md="4">
                    <v-card
                      outlined
                      class="card_section1"
                      min-width="200"
                      max-width="250"
                      max-height="250"
                    >
                      <p class="text_subQuestion mb-0">ขอข้อมูลเพิ่มเติม</p>
                      <p
                        class="text_question mb-0"
                        :style="{ color: '#EF5350' }"
                      >
                        {{ this.caseOverall.ALL_REQUIRE }}
                      </p>

                      <v-row align="center" justify="center">
                        <span class="text_attribute mr-3">UN: </span>
                        <span class="text_subQuestion mr-4">
                          {{ this.caseOverall.UN_REQUIRE }}</span
                        >
                        <span class="text_attribute mr-3">PD: </span>
                        <span class="text_subQuestion mr-4">{{
                          this.caseOverall.PD_REQUIRE
                        }}</span>
                        <span class="text_attribute mr-3">AD: </span>
                        <span class="text_subQuestion">{{
                          this.caseOverall.AD_REQUIRE
                        }}</span>
                      </v-row>
                    </v-card>
                  </v-col>
                  <v-col align="center" md="4">
                    <v-card
                      outlined
                      class="card_section1"
                      min-width="200"
                      max-width="250"
                      max-height="250"
                    >
                      <p class="text_subQuestion mb-0">เสร็จสิ้น</p>
                      <p
                        class="text_question mb-0"
                        :style="{ color: '#8BC34A' }"
                      >
                        {{ this.caseOverall.ALL_COMPLETE }}
                      </p>

                      <v-row align="center" justify="center">
                        <span class="text_attribute mr-3">UN: </span>
                        <span class="text_subQuestion mr-4">
                          {{ this.caseOverall.UN_COMPLETE }}</span
                        >
                        <span class="text_attribute mr-3">PD: </span>
                        <span class="text_subQuestion mr-4">{{
                          this.caseOverall.PD_COMPLETE
                        }}</span>
                        <span class="text_attribute mr-3">AD: </span>
                        <span class="text_subQuestion">{{
                          this.caseOverall.AD_COMPLETE
                        }}</span>
                      </v-row>
                    </v-card>
                  </v-col>
                </v-row>
              </v-col>
            </v-row>

            <!-- DATA TABLE -->
            <div class="contentTabBox">
              <v-tabs
                v-model="tab"
                centered
                grow
                show-arrows
                background-color="#FAFAFA"
                color="#00B5F8"
              >
                <v-tab v-for="(topic, key) in title_topicTabs" :key="key">
                  <div>
                    <span class="text_title">{{ topic.title }}</span>
                  </div>
                </v-tab>
                <v-tabs-items v-model="tab">
                  <v-tab-item
                    v-for="(contents, index) in caseDetail"
                    :key="index"
                  >
                    <v-card flat width="1264px">
                      <v-card-title>
                        <v-text-field
                          v-model="search"
                          append-icon="fa-search"
                          label="ค้นหารายงาน"
                          single-line
                          hide-details
                        ></v-text-field>
                      </v-card-title>

                      <v-data-table
                        light
                        :headers="
                          tab == 1 || tab == 4
                            ? table_headers
                            : table_headersOpt
                        "
                        :items="contents"
                        :items-per-page="5"
                        :search="search"
                      >
                        <!-- click on row: @click:row="displayCase" -->

                        <template v-slot:item.CaseTypeName="{ item }">
                          <v-chip :color="getColor(item.CaseTypeName)" dark>{{
                            item.CaseTypeName
                          }}</v-chip>
                        </template>

                        <template
                          v-if="tab == 0 || tab == 3"
                          v-slot:item.action="{ item }"
                        >
                          <v-col>
                            <v-row
                              class="text-left"
                              align="center"
                              justify="center"
                            >
                              <v-icon
                                class="mx-2 my-1"
                                color="primary"
                                @click="viewCase(item)"
                              >
                                mdi-eye
                              </v-icon>

                              <v-icon
                                class="mx-2 my-1"
                                color="dark-grey"
                                @click="editCase(item)"
                              >
                                mdi-pencil-circle
                              </v-icon>

                              <v-icon
                                class="mx-2 my-1"
                                color="pink"
                                @click="deleteCase(item)"
                              >
                                mdi-delete
                              </v-icon>
                            </v-row>
                          </v-col>
                        </template>

                        <template v-else v-slot:item.action="{ item }">
                          <v-icon small class="mr-2" @click="viewCase(item)">
                            fa-eye
                          </v-icon>
                        </template>
                      </v-data-table>
                    </v-card>
                  </v-tab-item>
                </v-tabs-items>
              </v-tabs>
            </div>
          </div>
        </div>
        <div v-else>
          <SkeletonLoader />
        </div>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import ReportFakeAds from "./display/ReportFakeAds";
import ReportProduct from "./display/ReportProduct";
import ReportSymptom from "./display/ReportSymptom";
import SkeletonLoader from "./actions/SkeletonLoader";
export default {
  inject: ["theme"],
  components: {
    ReportFakeAds,
    ReportProduct,
    ReportSymptom,
    SkeletonLoader
  },
  data: () => ({
    tab: null,
    search: "",
    dialog_createCase: false,
    displayCase: false,
    dp_caseTypeID: 0,
    dp_title: "",
    title_topicTabs: [
      { title: "ฉบับร่าง" },
      { title: "รอตอบรับ" },
      { title: "กำลังดำเนินการ" },
      { title: "ขอข้อมูลเพิ่มเติม" },
      { title: "เสร็จสิ้น" }
    ],
    table_headers: [
      {
        text: "รหัสรายงาน",
        align: "left",
        sortable: false,
        value: "CaseId",
        width: "10%"
      },
      { text: "ประเภทรายงาน", value: "CaseTypeName", width: "15%" },
      { text: "ชื่อผลิตภัณฑ์", value: "ProductName", width: "35%" },
      { text: "วันที่สร้าง", value: "CreatedDate", width: "15%" },
      { text: "วันที่อัปเดตล่าสุด", value: "UpdatedDate", width: "15%" },
      {
        text: "การจัดการ",
        value: "action",
        sortable: false,
        width: "10%",
        align: "center"
      }
    ],
    table_headersOpt: [
      {
        text: "รหัสรายงาน",
        align: "left",
        sortable: false,
        value: "CaseId",
        width: "10%"
      },
      { text: "ประเภทรายงาน", value: "CaseTypeName", width: "15%" },
      { text: "ชื่อผลิตภัณฑ์", value: "ProductName", width: "35%" },
      { text: "วันที่สร้าง", value: "CreatedDate", width: "15%" },
      { text: "วันที่อัปเดตล่าสุด", value: "UpdatedDate", width: "15%" },
      {
        text: "การจัดการ",
        value: "action",
        sortable: false,
        width: "10%",
        align: "center"
      }
    ],
    choice_caseType: [
      {
        title: "รายงานอาการไม่พึงประสงค์",
        icon: "fa-user-slash",
        caseTypeID: "1",
        info: "ผู้เกิดอาการไม่พึงประสงค์จากการใช้ผลิตภัณฑ์สุขภาพ"
      },
      {
        title: "รายงานผลิตภัณฑ์ต้องสงสัย",
        icon: "fa-pills",
        caseTypeID: "2",
        info: "ผลิตภัณฑ์สุขภาพที่ไม่ได้มาตรฐานหรือส่งผลข้างเคียง"
      },
      {
        title: "รายงานโฆษณาเกินจริง",
        icon: "fa-bullhorn",
        caseTypeID: "3",
        info: "โฆษณาเกินจริงหรือเข้าข่ายห้ามโฆษณา"
      }
    ],
    userAuth: {
      userID: "",
      userToken: ""
    },
    userInfo: [],
    caseOverall: [],
    caseDetail: [],
    // query from display caseSymptom
    s1_pt_info: {
      pt_fname: "",
      pt_lname: "",
      pt_gender: "",
      pt_age: "",
      pt_weight: "",
      pt_pid: "",
      pt_telno: "",
      is_checkForeigner: false,
      pt_national: "",
      pt_national_info: ""
    },
    s1_pt_address: {
      pt_ads_address: "",
      pt_ads_province: "",
      pt_ads_district: "",
      pt_ads_subDistrict: ""
    },
    s1_pt_history: {
      pt_his_favHospital: "",
      pt_his_conditionInfo: ""
    },
    s1_his_arr_disease: [],
    s1_his_arr_drugAllegy: [],
    s1_photos_show: [],
    s1_st_info: {
      st_symptomInfo: "",
      st_date: "",
      st_dangerType: "",
      st_dangerList: "",
      st_dangerList_ID: 10,
      st_dangerSeverityDetail: "",
      st_treatment: "",
      st_treatment_ID: 3,
      st_hospital: "",
      st_hospital_province: ""
    },
    s1_st_discovery: {
      st_dis_address: "",
      st_dis_province: "",
      st_dis_district: "",
      st_dis_subDistrict: ""
    },
    s2_3_information: [],
    // query from display caseProduct
    s1_user_info: {
      us_hasUser: false,
      us_fName: "",
      us_lName: "",
      us_gender: "",
      us_age: "",
      us_weight: "",
      us_contact: ""
    },
    s1_pd_discover: {
      pdd_address: "",
      pdd_province: "",
      pdd_district: "",
      pdd_subDistrict: ""
    },
    s2_pd_detail_full: {
      pd_pdTypeName: "",
      pd_pdTypeIndex: 1,
      pd_nameTH: "",
      pd_cNameTH: "",
      pd_cNameEN: "",
      pd_fNameTH: "",
      pd_fNameEN: "",
      pd_hpTypeName: "",
      pd_hpTypeIndex: 1,
      pd_hpInfo: ""
    },
    s2_pd_label: {
      pd_hasLabel: false,
      pd_hasLicense: false
    },
    s2_pd_licenseInfo: {
      pdlc_number: "",
      pdlc_status: "",
      pdlc_isMatch: "0"
    },
    s2_pd_manufacturer: {
      pd_mf_owner: "",
      pd_mf_name: "",
      pd_mf_address: "",
      pd_mf_lot: "",
      pd_mf_mfd: "",
      pd_mf_exd: "",
      pd_mf_newcode: ""
    },
    s2_pd_usage: {
      pd_us_KindName: "",
      pd_us_KindIndex: 0,
      pd_us_KindInfo: "",
      pd_us_InstName: "",
      pd_us_InstIndex: 0,
      pd_us_InstInfo: "",
      pd_us_volume: "",
      pd_us_price: "",
      pd_us_expected: "",
      pd_us_labelInfo: ""
    },
    s2_pd_check: {
      isCheckDMSC: false,
      isCheckQuality: false,
      isCheckAds: false,
      isCheckTestkit: false
    },
    s2_pd_checkInfo: {
      check_DMSC_status: "0",
      check_DMSC_info: "",
      check_quality_status: "0",
      check_quality_info: "",
      check_ads_status: "0",
      check_ads_info: ""
    },
    s2_drugSet: [],
    s2_testkit: [],

    // query for display caseFakeAds
    s1_ads_detail: "",
    s2_pd_detail: {
      pd_pdTypeName: "",
      pd_pdTypeIndex: 1,
      pd_nameTH: "",
      pd_cNameTH: "",
      pd_cNameEN: "",
      pd_fNameTH: "",
      pd_fNameEN: ""
    },
    s3_src_detail: {
      src_srcTypeName: "",
      src_srcTypeIndex: 0, //ChannelID
      src_subSrcTypeName: "", //subName
      src_subSrcTypeIndex: 0, //ChannelSubID
      src_subInfoName: "", //Name
      src_dateOnAir: "", //date
      src_timeOnAir: "", //Period
      src_adrs: "",
      src_prov: "",
      src_dist: "",
      src_subDist: "",
      src_pName: "",
      src_pTel: "",
      src_pContact: "",
      src_pLicense: "",
      src_pCar: "",
