  methods: {
    async fetchMainUser() {
      this.updateUserAuth_local();
      await fetch("http://www.tawainetwork.org/webBackendPHP/getMainuser.php", {
        method: "POST",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json"
        },
        body: JSON.stringify({
          userID: this.userAuth.userID,
          userToken: this.userAuth.userToken
        })
      })
        .then(res => {
          return res.json();
        })
        .then(dataRes => {
          // console.log(' - DATA: ', dataRes)
          if (dataRes[0]["status"] == "success") {
            this.userInfo = dataRes[1];
            this.caseOverall = dataRes[2];
            this.caseDetail.push(dataRes[3]);
            this.caseDetail.push(dataRes[4]);
            this.caseDetail.push(dataRes[5]);
            this.caseDetail.push(dataRes[6]);
            this.caseDetail.push(dataRes[7]);
          } else {
            this.$router.push("login");
          }
        })
        .catch(err => {
          console.log("ERR.fetchMainUser: ", err);
        });
    },
    async requestDraftCase(caseType) {
      // DraftCase
      await fetch(
        "http://www.tawainetwork.org/webBackendPHP/postCreate_draftCase.php",
        {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json"
          },
          body: JSON.stringify({
            userID: localStorage.getItem("userID"),
            caseTypeID: caseType
          })
        }
      )
        .then(res => {
          return res.json();
        })
        .then(dataRes => {
          console.log("request Create Draft", dataRes);
          if (dataRes[0]["status"] == "success") {
            switch (caseType.toString()) {
              case "1":
                var userProp_pd = {
                  userID: localStorage.getItem("userID"),
                  userRole: localStorage.getItem("userRole")
                };
                var caseProp_pd = {
                  caseID: dataRes[0]["caseID"],
                  caseType: "1",
                  manageMode: "create"
                };
                this.$store.dispatch("setUserProperties", userProp_pd);
                this.$store.dispatch("setCaseProperties", caseProp_pd);
                this.$router.push("createReport_symptom");
                break;
              case "2":
                var userProp_pd = {
                  userID: localStorage.getItem("userID"),
                  userRole: localStorage.getItem("userRole")
                };
                var caseProp_pd = {
                  caseID: dataRes[0]["caseID"],
                  caseType: "2",
                  manageMode: "create"
                };
                this.$store.dispatch("setUserProperties", userProp_pd);
                this.$store.dispatch("setCaseProperties", caseProp_pd);
                this.$router.push("createReport_product");
                break;

              case "3":
                var userProp_ads = {
                  userID: localStorage.getItem("userID"),
                  userRole: localStorage.getItem("userRole")
                };
                var caseProp_ads = {
                  caseID: dataRes[0]["caseID"],
                  caseType: "3",
                  manageMode: "create"
                };
                this.$store.dispatch("setUserProperties", userProp_ads);
                this.$store.dispatch("setCaseProperties", caseProp_ads);
                this.$router.push("createReport_fakeAds");
                break;

              default:
                break;
            }
          } else {
            // Create draft failed
          }
        })
        .catch(err => {
          console.log("ERR.createDraft.Case: ", err);
        });
    },
    async checkUserAuth_local() {
      let checkAuth = localStorage.getItem("userToken");
      if (checkAuth) {
        let userID = localStorage.getItem("userID");
        await fetch(
          "http://www.tawainetwork.org/webBackendPHP/recheckSession.php",
          {
            method: "POST",
            headers: {
              Accept: "application/json",
              "Content-Type": "application/json"
            },
            body: JSON.stringify({
              userID: userID,
              userToken: checkAuth
            })
          }
        )
          .then(res => {
            return res.json();
          })
          .then(dataRes => {
            // console.log(' - DATA: ', dataRes)
            if (dataRes[0]["status"] == "failed") {
              this.$router.push("login");
              localStorage.removeItem("userToken");
              localStorage.removeItem("userID");
              localStorage.removeItem("userRole");
            } else {
              this.fetchMainUser();
            }
          })
          .catch(err => {
            console.log("ERR.requestAccess.Session_MainUser: ", err);
          });
      } else {
        this.$router.push("/");
      }
    },
    async queryFakeAds(caseID) {
