    async queryPatient(caseID) {
      await fetch(
        "http://www.tawainetwork.org/webBackendPHP/getCaseDetailed.php",
        {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json"
          },
          body: JSON.stringify({
            userID: localStorage.getItem("userID"),
            caseID: caseID,
            caseTypeID: "1"
          })
        }
      )
        .then(res => {
          return res.json();
        })
        .then(dataRes => {
          // console.log("getDetailed.Patient - DATA: ", dataRes);
          if (dataRes[0]["status"] == "success") {
            let temp_caseDate = "";
            if (dataRes[1]["CaseDate"]) {
              temp_caseDate = dataRes[1]["CaseDate"]["date"];
              temp_caseDate = temp_caseDate.substr(0, 10);
            }

            this.s1_pt_info = {
              pt_fname: dataRes[1]["PFName"],
              pt_lname: dataRes[1]["PLName"],
              pt_gender: dataRes[1]["Gender"],
              pt_age: dataRes[1]["Age"],
              pt_weight: dataRes[1]["Weight"],
              pt_pid: dataRes[1]["PersonalId"],
              pt_telno: dataRes[1]["TelNo"],
              is_checkForeigner: parseInt(dataRes[1]["IsForeigner"]),
              pt_national: dataRes[1]["Nationality"],
              pt_national_info: dataRes[1]["Nationality_info"]
            };
            this.s1_pt_address = {
              pt_ads_address: dataRes[1]["PAdr"],
              pt_ads_province: dataRes[1]["PProvince"],
              pt_ads_district: dataRes[1]["PDistrict"],
              pt_ads_subDistrict: dataRes[1]["PSubDistrict"]
            };
            this.s1_pt_history = {
              pt_his_favHospital: dataRes[1]["MedTreatmentPlc"],
              pt_his_conditionInfo: dataRes[1]["Condition"]
            };
            this.s1_st_info = {
              st_symptomInfo: dataRes[1]["SymptnDetails"],
              st_date: temp_caseDate,
              st_dangerType: dataRes[1]["GroupName"],
              st_dangerList: dataRes[1]["SeverityName"],
              st_dangerList_ID: dataRes[1]["SeverityId"],
              st_dangerSeverityDetail: dataRes[1]["comment"],
              st_treatment: dataRes[1]["Treatment_Name"],
              st_treatment_ID: dataRes[1]["TreatmentId"],
              st_hospital: dataRes[1]["TreatmentPlace"],
              st_hospital_province: dataRes[1]["TreatmentProvince"]
            };
            this.s1_st_discovery = {
              st_dis_address: dataRes[1]["FndAdr"] ? dataRes[1]["FndAdr"] : "",
              st_dis_province: dataRes[1]["FndProvince"]
                ? dataRes[1]["FndProvince"]
                : "",
              st_dis_district: dataRes[1]["FndDistrict"]
                ? dataRes[1]["FndDistrict"]
                : "",
              st_dis_subDistrict: dataRes[1]["FndSubDistrict"]
                ? dataRes[1]["FndSubDistrict"]
                : ""
            };

            if (dataRes[3].length > 0) {
              this.s1_his_arr_disease = dataRes[3];
            }
            if (dataRes[4].length > 0) {
              this.s1_his_arr_drugAllegy = dataRes[4];
            }

            // Get Symptom images
            if (dataRes[1]["Photos"]) {
              let splitImgPath = dataRes[1]["Photos"].split(",");
              if (splitImgPath.length > 1) {
                splitImgPath.pop();
              }
              this.s1_photos_show = splitImgPath;
            } else {
              this.s1_photos_show = [];
            }

            // Get Product and Channel
            this.queryProductAndChannel(caseID);
          }
        })
        .catch(err => {
          console.log("ERR.request.getCase.Patient: ", err);
        });
    },
    async queryProductAndChannel(caseID) {
      await fetch(
        "http://www.tawainetwork.org/webBackendPHP/getproductDetailed_onsymptom.php",
        {
          method: "POST",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json"
          },
          body: JSON.stringify({
            userID: localStorage.getItem("userID"),
            caseID: caseID
          })
        }
      )
        .then(res => {
          return res.json();
        })
        .then(dataRes => {
          // console.log("getDetailed.ProductAndChannel - DATA: ", dataRes);
          if (dataRes[0]["status"] == "success") {
            this.s2_3_information = dataRes;
          }
        })
        .catch(err => {
          console.log("ERR.request.getCase.ProductAndChannel: ", err);
        });
    },
    async viewCase(item) {
      // console.log('onView ', item);
      this.displayCase = true;
      if (item.CaseTypeName == "รายงานโฆษณาเกินจริง") {
        this.dp_caseTypeID = 3;
        this.dp_title = item.CaseTypeName + " #" + item.CaseId;
        this.queryFakeAds(item.CaseId);
      } else if (item.CaseTypeName == "รายงานผลิตภัณฑ์ต้องสงสัย") {
        this.dp_caseTypeID = 2;
        this.dp_title = item.CaseTypeName + " #" + item.CaseId;
        this.queryProduct(item.CaseId);
      } else if (item.CaseTypeName == "รายงานอาการไม่พึงประสงค์") {
        this.dp_caseTypeID = 1;
        this.dp_title = item.CaseTypeName + " #" + item.CaseId;
        this.queryPatient(item.CaseId);
      }
    },
    editCase(item) {
      // console.log("onEdit.CASE# ", item.CaseId);
      var caseTypeID = 0;
      if (item.CaseTypeName == "รายงานอาการไม่พึงประสงค์") {
        caseTypeID = 1;
      } else if (item.CaseTypeName == "รายงานผลิตภัณฑ์ต้องสงสัย") {
        caseTypeID = 2;
      } else {
        caseTypeID = 3;
      }
      var userProp = {
        userID: localStorage.getItem("userID"),
        userRole: localStorage.getItem("userRole")
      };
      var caseProp = {
        caseID: item.CaseId,
        caseType: caseTypeID,
        manageMode: "edit"
      };

      this.$store.dispatch("setUserProperties", userProp);
      this.$store.dispatch("setCaseProperties", caseProp);

      if (caseTypeID == 1) {
        this.$router.push("createReport_symptom");
      } else if (caseTypeID == 2) {
        this.$router.push("createReport_product");
      } else {
        this.$router.push("createReport_fakeAds");
      }
    },
    deleteCase(item) {
      console.log("onDelete ", item);
    },
    getColor(caseType) {
      if (caseType == "รายงานโฆษณาเกินจริง") return "orange";
      else if (caseType == "รายงานผลิตภัณฑ์ต้องสงสัย") return "red";
      else return "purple";
    },
    updateUserAuth_local() {
      this.userAuth.userID = localStorage.getItem("userID");
      this.userAuth.userToken = localStorage.getItem("userToken");
    }
  },
  created() {
    var userProp = {
      userID: localStorage.getItem("userID"),
      userRole: localStorage.getItem("userRole")
    };
    var caseProp = {
      caseID: "",
      caseType: "",
      manageMode: ""
    };
    this.$store.dispatch("setUserProperties", userProp);
    this.$store.dispatch("setCaseProperties", caseProp);
    this.userProperties = this.$store.getters.getUserProperties;
    this.caseProperties = this.$store.getters.getCaseProperties;
  },
  mounted() {
    this.checkUserAuth_local();
  }
};
</script>

